'use client'

import { PublicKey } from '@solana/web3.js'
import { useMemo, useState, useEffect } from 'react'
import { ExplorerLink } from '../../components/cluster/cluster-ui'
import { useCoinFlipProgram } from '../../components/coin-flip/coin-flip-data-access'
import { ellipsify } from '@/lib/utils'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../../components/ui/card'
import { useWallet } from '@solana/wallet-adapter-react'
import { Settings, DollarSign, TrendingUp, Users } from 'lucide-react'

const LAMPORTS_PER_SOL = 1_000_000_000

export function AdminGameCreate() {
  const { initialize, accounts } = useCoinFlipProgram()
  const wallet = useWallet()
  const [feePercentage, setFeePercentage] = useState(5)

  const existingGameState = useMemo(() => {
    if (!wallet.publicKey || !accounts.data) return null
    return accounts.data.find((account: any) => account.account.owner.equals(wallet.publicKey!))
  }, [accounts.data, wallet.publicKey])

  const handleInitialize = () => {
    if (!wallet.publicKey || existingGameState) return
    initialize.mutateAsync({ owner: wallet.publicKey, fee_percentage: feePercentage })
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Settings className="h-5 w-5" />
          Initialize Game State
        </CardTitle>
        <CardDescription>Create a new coin flip game instance</CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-center gap-4">
            <label className="text-sm font-medium text-gray-700">House Fee:</label>
            <input
              type="number"
              min={0}
              max={100}
              value={feePercentage}
              onChange={(e) => setFeePercentage(Number(e.target.value))}
              className="w-20 px-3 py-2 border rounded-md"
            />
            <span className="text-sm">%</span>
          </div>
          
          <Button 
            onClick={handleInitialize} 
            disabled={initialize.isPending || !!existingGameState}
            className="w-full"
          >
            {initialize.isPending ? 'Initializing...' : 
             existingGameState ? 'Game State Already Exists' : 
             'Initialize Game State'}
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}

export function AdminGameManagement() {
  const { accounts, deposit_to_gamestate, withdraw } = useCoinFlipProgram()
  const wallet = useWallet()
  const [vaultAmount, setVaultAmount] = useState<number>(0)

  // Find user's game state
  const userGameState = useMemo(() => {
    if (!wallet.publicKey || !accounts.data) return null
    return accounts.data.find((account: any) => account.account.owner.equals(wallet.publicKey!))
  }, [accounts.data, wallet.publicKey])

  // Reset form after operations
  useEffect(() => {
    if (deposit_to_gamestate.isSuccess || withdraw.isSuccess) {
      setVaultAmount(0)
    }
  }, [deposit_to_gamestate.isSuccess, withdraw.isSuccess])

  const handleVaultOperation = async (operation: 'deposit' | 'withdraw') => {
    if (!wallet.publicKey || !vaultAmount || vaultAmount <= 0) return

    const amountLamports = vaultAmount * LAMPORTS_PER_SOL
    
    try {
      if (operation === 'deposit') {
        await deposit_to_gamestate.mutateAsync({ owner: wallet.publicKey, amount: amountLamports })
      } else {
        await withdraw.mutateAsync({ owner: wallet.publicKey, amount: amountLamports })
      }
    } catch (error) {
      console.error(`${operation} failed:`, error)
    }
  }

  if (!userGameState) {
    return (
      <Card>
        <CardContent className="pt-6">
          <div className="text-center py-8">
            <p className="text-gray-600">No game state found. Please initialize one first.</p>
          </div>
        </CardContent>
      </Card>
    )
  }

  const gameData = userGameState.account

  return (
    <div className="space-y-6">
      {/* Game Stats */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <TrendingUp className="h-5 w-5" />
            Game Statistics
          </CardTitle>
          <CardDescription>
            <ExplorerLink 
              path={`account/${userGameState.publicKey}`} 
              label={ellipsify(userGameState.publicKey.toString())}
            />
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="text-center p-4 bg-blue-50 rounded-lg">
              <div className="text-2xl font-bold text-blue-600">
                {gameData.totalGames.toString()}
              </div>
              <div className="text-sm text-blue-700">Total Games</div>
            </div>
            
            <div className="text-center p-4 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">
                {(gameData.totalAmount.toNumber() / LAMPORTS_PER_SOL).toFixed(4)}
              </div>
              <div className="text-sm text-green-700">Vault Balance (SOL)</div>
            </div>
            
            <div className="text-center p-4 bg-purple-50 rounded-lg">
              <div className="text-2xl font-bold text-purple-600">
                {gameData.feePercentage.toString()}%
              </div>
              <div className="text-sm text-purple-700">House Fee</div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Vault Management */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <DollarSign className="h-5 w-5" />
            Vault Management
          </CardTitle>
          <CardDescription>Deposit or withdraw funds from the game vault</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div className="flex items-center gap-2">
              <input
                type="number"
                min={0}
                step={0.1}
                placeholder="Amount in SOL"
                value={vaultAmount || ''}
                onChange={(e) => setVaultAmount(Number(e.target.value))}
                disabled={deposit_to_gamestate.isPending || withdraw.isPending}
                className="flex-1 px-3 py-2 border rounded-md text-gray-900"
              />
              <span className="text-sm text-gray-700">SOL</span>
            </div>
            
            <div className="flex gap-2">
              <Button 
                onClick={() => handleVaultOperation('deposit')} 
                disabled={deposit_to_gamestate.isPending || withdraw.isPending || !vaultAmount}
                className="flex-1"
              >
                {deposit_to_gamestate.isPending ? 'Depositing...' : 'Deposit'}
              </Button>
              
              <Button 
                onClick={() => handleVaultOperation('withdraw')} 
                disabled={withdraw.isPending || deposit_to_gamestate.isPending || !vaultAmount}
                variant="destructive" 
                className="flex-1"
              >
                {withdraw.isPending ? 'Withdrawing...' : 'Withdraw'}
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

export default function AdminPage() {
  const { accounts, getProgramAccount } = useCoinFlipProgram()
  const wallet = useWallet()

  if (!wallet.publicKey) {
    return (
      <div className="max-w-4xl mx-auto px-4 py-8">
        <div className="text-center py-12">
          <h1 className="text-2xl font-bold mb-4">Admin Panel</h1>
          <p className="text-gray-600">Please connect your wallet to access admin features.</p>
        </div>
      </div>
    )
  }

  if (getProgramAccount.isLoading) {
    return (
      <div className="max-w-4xl mx-auto px-4 py-8">
        <div className="flex justify-center py-8">Loading...</div>
      </div>
    )
  }
  
  if (!getProgramAccount.data?.value) {
    return (
      <div className="max-w-4xl mx-auto px-4 py-8">
        <div className="text-center py-8">
          <h1 className="text-2xl font-bold mb-4">Admin Panel</h1>
          <p>Program account not found. Make sure you have deployed the program.</p>
        </div>
      </div>
    )
  }

  const userGameState = accounts.data?.find((account: any) => 
    account.account.owner.equals(wallet.publicKey!)
  )

  return (
    <div className="max-w-4xl mx-auto px-4 py-8 space-y-8">
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Admin Panel</h1>
        <p className="text-gray-600 mt-2">Manage your coin flip game instance</p>
      </div>

      {!userGameState ? (
        <AdminGameCreate />
      ) : (
        <AdminGameManagement />
      )}
    </div>
  )
}